import { type GEvent } from '@huangjs888/gesture';
import Gallery from '../gallery';
import SingleGallery from '../singleGallery';

export default function pointerEnd(this: Gallery | SingleGallery, e: GEvent) {
  if (this._isClose) {
    return;
  }
  let target = '';
  if (this instanceof Gallery) {
    target = this._moveTarget;
  }
  if (e.pointer.length === 0) {
    // 
    this._fgBehavior = 0;
    if (this instanceof Gallery) {
      this._moveTarget = 'none';
    }
  } else if (this._fgBehavior === 1) {
    // 
    if (this instanceof Gallery) {
      // slide
      if (target === 'outside') {
        const size = this.getItemSize();
        const index = size === 0 ? 0 : -this._translate / this.getItemSize();
        this.slide(Math.round(index));
      }
    }
    return;
  }
  const point = e.point[2];
  if (this instanceof Gallery) {
    if (this.isTransitioning()) {
      return;
    }
    if (target === 'closures') {
      this.originTransform(0, 0, 1, 1, 300);
      return;
    }
    const { entity } = (this._images && this._images[this._activeIndex]) || {};
    if (entity) {
      if (entity.isTransitioning()) {
        return;
      }
      entity.resetBounce(point, this._onePoint);
    }
    // swiper
    const size = this.getItemSize();
    let index = this._activeIndex;
    if (target === 'outside') {
      index = size === 0 ? 0 : -this._translate / this.getItemSize();
    }
    // Math.round, slide_activeIndex
    this.slide(Math.round(index));
  } else {
    const { entity } = this._image || {};
    if (entity) {
      if (entity.isTransitioning()) {
        return;
      }
      entity.resetBounce(point, this._onePoint);
    }
  }
}
